import { Component } from '@angular/core';

@Component({
  selector: 'app-warnings',
  templateUrl: 'warnings.component.html',
  styleUrls: ['warnings.component.scss']
})
export class WarningsPage {
  public title = 'Warnings';
  public errorMessage: string;
  public searchFlag = false;
  public listFlag = true;
  public profileFlag = false;
  public settingsFlag = false;
  public filterFlag = false;

  switchSearch() {
    if (this.searchFlag === false) {
        this.searchFlag = true;
        this.listFlag = false;
    } else {
        this.searchFlag = false;
        this.profileFlag = false;
        this.settingsFlag = false;
        this.filterFlag = false;
        this.listFlag = true;
    }
}

switchProfile() {
    if (this.profileFlag === false) {
        this.profileFlag = true;
        this.listFlag = false;
    } else {
        this.searchFlag = false;
        this.profileFlag = false;
        this.settingsFlag = false;
        this.filterFlag = false;
        this.listFlag = true;
    }
}

switchSettings() {
    if (this.settingsFlag === false) {
        this.settingsFlag = true;
        this.listFlag = false;
    } else {
        this.searchFlag = false;
        this.profileFlag = false;
        this.settingsFlag = false;
        this.filterFlag = false;
        this.listFlag = true;
    }
}

switchFilter() {
    if (this.filterFlag === false) {
        this.filterFlag = true;
        this.listFlag = false;
    } else {
        this.searchFlag = false;
        this.profileFlag = false;
        this.settingsFlag = false;
        this.filterFlag = false;
        this.listFlag = true;
    }
}
}
